@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

html, body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto';
    overflow-x: hidden;
}

body {
    position: relative;
}

iframe {
    display: none;
}

button {
    cursor: pointer;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

@keyframes headerFadeIn {
    from { top: -120px; }
    to { top: 0; }
}

@keyframes introZoom {
    from {mask-size: 1%;}
    to {mask-size: 1000%;}
}

@keyframes introExpandLine {
    from { width: 0% }
    to { width: 80% }
}

@keyframes fadeIn {
    from { opacity: 0 }
    to { opacity: 1 }
}

@keyframes introHeadingFade {
    from { bottom: -100px }
    to { bottom: 0 }
}

@keyframes introSubheadingFade {
    from { top: -50px }
    to { top: 0 }
}

@keyframes getInTouchFade {
    from { left: -50px }
    to { left: 0px }
}

